/* DARK THEME */
.App.dark {
  background: #020C31; }

.Nav.dark {
  margin-bottom: 10px; }

.Nav.navBar.dark {
  border-bottom: 2px solid #4A6846; }

.nav-link.dark {
  color: #4A6846;
  font-family: "Jura", monospace; }
  .nav-link.dark:hover {
    -webkit-box-shadow: 0 0 70px 15px rgba(196, 241, 190, 0.7);
            box-shadow: 0 0 70px 15px rgba(196, 241, 190, 0.7);
    color: #C4F1BE; }

.nav-link.dark.active {
  background: #4A6846;
  color: #020C31;
  font-weight: 900; }
  .nav-link.dark.active:hover {
    background: #020C31;
    color: #C4F1BE; }

.home-link.dark {
  color: #020C31;
  text-shadow: 0 0 15px #C4F1BE; }

h1.dark {
  border: none;
  color: #020C31;
  font-family: "Jura", monospace;
  font-size: 70pt;
  text-shadow: rgba(196, 241, 190, 0.7) 0 0 30px; }

.sub-title.dark {
  color: rgba(255, 170, 104, 0.4); }

.github.dark {
  background-image: url("../assets/github-icons/github-dark.svg"); }
  .github.dark:hover {
    background-image: url("../assets/github-icons/github-dark-hover.svg"); }

.email.dark {
  background-image: url("../assets/email-icons/email-dark.svg"); }
  .email.dark:hover {
    background-image: url("../assets/email-icons/email-dark-hover.svg"); }

.linkedIn.dark {
  background-image: url("../assets/linkedIn-icons/linkedIn-dark.svg"); }
  .linkedIn.dark:hover {
    background-image: url("../assets/linkedIn-icons/linkedIn-dark-hover.svg"); }

.github.dark:hover,
.email.dark:hover,
.linkedIn.dark:hover {
  -webkit-box-shadow: 0 0 70px 15px rgba(196, 241, 190, 0.7);
          box-shadow: 0 0 70px 15px rgba(196, 241, 190, 0.7); }

#toggle.dark {
  background-color: #4A6846; }
  #toggle.dark label {
    background-color: #C4F1BE;
    -webkit-box-shadow: 0 0 20px 1px rgba(196, 241, 190, 0.7);
            box-shadow: 0 0 20px 1px rgba(196, 241, 190, 0.7);
    color: #FAF9F6;
    font-family: "Jura", monospace; }
  #toggle.dark input:checked + label {
    background-color: #008; }

.Projects.dark button {
  background-image: url("../assets/arrow-icons/arrow-dark.svg"); }
  .Projects.dark button:hover {
    background-image: url("../assets/arrow-icons/arrow-dark-hover.svg"); }

.Projects.dark .project-links .repo-icon.dark {
  background-image: url("../assets/github-icons/github-dark.svg"); }
  .Projects.dark .project-links .repo-icon.dark:hover {
    background-image: url("../assets/github-icons/github-dark-hover.svg"); }

.Projects.dark .project-links .repo-link.dark {
  color: #4A6846; }
  .Projects.dark .project-links .repo-link.dark:hover {
    color: #C4F1BE; }

.About.dark h2 {
  color: rgba(255, 170, 104, 0.4);
  font-size: 35pt; }

.About.dark p {
  color: #4A6846; }

.About.dark .resume-wrapper.dark h2 {
  color: rgba(255, 170, 104, 0.4);
  font-size: 20pt; }

.About.dark .resume-wrapper.dark .resume-label.dark {
  background: rgba(196, 241, 190, 0.7);
  color: #020C31; }
  .About.dark .resume-wrapper.dark .resume-label.dark .resume.dark {
    background: rgba(196, 241, 190, 0.7);
    color: #020C31; }
    .About.dark .resume-wrapper.dark .resume-label.dark .resume.dark:hover {
      -webkit-box-shadow: 0 0 70px 15px rgba(196, 241, 190, 0.7);
              box-shadow: 0 0 70px 15px rgba(196, 241, 190, 0.7);
      background: #020C31;
      color: #C4F1BE; }



